//    {"_id": "60f25856d8e0a44090298a08","Title":"Captain Phillips","Year":"2013","Rated":"PG-13","Released":"11 Oct 2013","Runtime":"134 min","Genre":"Adventure, Biography, Crime","Director":"Paul Greengrass","Writer":"Billy Ray, Richard Phillips, Stephan Talty","Actors":"Tom Hanks, Barkhad Abdi, Barkhad Abdirahman","Plot":"Captain Phillips is a multi-layered examination of the 2009 hijacking of the U.S. container ship Maersk Alabama by a crew of Somali pirates. It is - through director Paul Greengrass's distinctive lens - simultaneously a pulse-pounding thriller, and a complex portrait of the myriad effects of globalization. The film focuses on the relationship between the Alabama's commanding officer, Captain Richard Phillips (two time Academy Award-winner Tom Hanks), and the Somali pirate captain, Muse (Barkhad Abdi), who takes him hostage. Phillips and Muse are set on an unstoppable collision course when Muse and his crew target Phillips' unarmed ship; in the ensuing standoff, 145 miles off the Somali coast, both men will find themselves at the mercy of forces beyond their control.","Language":"English, Somali","Country":"United States","Awards":"Nominated for 6 Oscars. 16 wins & 154 nominations total","Poster":"https://m.media-amazon.com/images/M/MV5BODhiZWRhMjctNDUyMS00NmUwLTgwYmItMjJhOWNkZWQ3ZTQxXkEyXkFqcGdeQXVyMTMxODk2OTU@._V1_SX300.jpg","Ratings":[{"Source":"Internet Movie Database","Value":"7.8/10"},{"Source":"Rotten Tomatoes","Value":"93%"},{"Source":"Metacritic","Value":"82/100"}],"Metascore":"82","imdbRating":"7.8","imdbVotes":"431,060","imdbID":"tt1535109","Type":"movie","DVD":"13 Nov 2015","BoxOffice":"$107,100,855","Production":"Scott Rudin Productions, Trigger Street Productions, Michael De Luca","Website":"N/A","Response":"True"},
